import React, { Component, createRef } from "react";

export default class Output extends Component {
  constructor(props) {
    super();
    this.output = createRef();
  }

  componentDidMount() {
    this.showOutput();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.css !== this.props.css || prevProps.xml !== this.props.xml) {
      this.showOutput();
    }
  }

  showOutput = () => {
    if (this.output.current) {
      let { xml, css } = this.props;
      let document = this.output.current.contentWindow.document;
      let style = document.createElement("style");
      style.innerText = css;
      document.head.append(style);
      document.body.innerHTML = xml;
    }
  };

  render() {
    const { name } = this.props;
    return (
      <div className="window">
        <header className="header flex container">
          <div className="control-bar flex">
            <div className="control close"></div>
            <div className="control minimize"></div>
            <div className="control maximize"></div>
          </div>
          <h2 className="name">{name}</h2>
        </header>

        <iframe
          title="HTML/CSS Preview"
          ref={this.output}
          id="code_result"
          width="100%"
          height="100%"
        ></iframe>
      </div>
    );
  }
}
